import {labels} from 'data/dataLabels'
import { COUNTRIES } from "data/countries";

// fukcja ustawiajaca dane do chartu 
export const getCurrentMIlitaryPowerData = countriesNow => {

    if(countriesNow.length === 0){ // sprawdzenie czy tablica aktywnych krajow nie jest pusta
      return [];// jak jest pusta zwóc pusa tablice 
    }
    const dataArray = []; // ustawienia pojemnika na dane

    labels.forEach(data => { // przucenie w pojemnik tablic z warosciami poczatkowymi (dla kazdej elemelentu danych ze zbioru)
      dataArray.push(['', 0]);
    })
 
    // fukcja sumujaca dane z aktywnych krajów
    const accumulatedCountriesStat = countriesNow.reduce((accumulator, currentCountry) => { // fukcja redukuje dane ze zbioru
      const currentCountryInstance = COUNTRIES.find( // pysukanie kraju
        country => country["kod"] === currentCountry
      );
      labels.forEach((label, index) => { // zwiekszenie wszysatkich wartosci kluczy o elementy z danego kraju 
        if(accumulator[index][0] === '') accumulator[index][0] = label.key 
        accumulator[index][1] += parseInt(currentCountryInstance[label.key], 10);
      })
      return accumulator; // zwrócenie sumy
    }, dataArray) // wartosc startowa

    // przetwoprzenie danych 
    return accumulatedCountriesStat.map(stats => {
      const [labelKey, statValue] = stats; // getter i setter 
      const sortedCountry = COUNTRIES.sort((a,b)=> parseInt(a[labelKey],10) > parseInt(b[labelKey],10) ? -1 : 1); // soprtowanie wartosci od najwikszej
      const smallerValueInCurrentCategory = sortedCountry.find(country => parseInt(country[labelKey],10) < statValue); // znalezienie obecnej wartosci (sumy) w liscie wszystkich krajów
      const index = smallerValueInCurrentCategory ? sortedCountry.indexOf(smallerValueInCurrentCategory) : sortedCountry.length; // ustalenie wartosci miejsca do wyswietlenia
      const labelName = labels.find(currentLabel => currentLabel.key === labelKey).name; // ustawienie etykiety z tym miejscem
      return [`${labelName} - #${index+1}`, statValue] // zwrócenie tych wartosci jako tablcy
    })
  };