import React from 'react'
import { BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Bar } from 'recharts'
import { data } from './MapLegend.data';

const MapLegend = () => {

    return (
        <BarChart width={100} height={500} data={data} style={{
            top: '20vh',
            left: '-50px',
            position: 'absolute'
        }} >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis  />
            <Tooltip />
            <Bar dataKey="a" stackId="a" fill="#99CCFF" />
            <Bar dataKey="b" stackId="a" fill="#6699CC" />
            <Bar dataKey="c" stackId="a" fill="#336699" />
            <Bar dataKey="d" stackId="a" fill="blue" />
            <Bar dataKey="e" stackId="a" fill="#66CC33" />
            <Bar dataKey="f" stackId="a" fill="green" />
            <Bar dataKey="g" stackId="a" fill="#FFFF66" />
            <Bar dataKey="h" stackId="a" fill="yellow" />
            <Bar dataKey="i" stackId="a" fill="#FF3333" />
            <Bar dataKey="j" stackId="a" fill="black" />
        </BarChart>
    )
}

export default MapLegend;