import React, { useState } from "react";
import { COUNTRIES } from "data/countries";
import { Map } from "../../components/Map/Map";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import { MIlitaryPowerChart } from "../../components/MilitaryPowerChart/MilitaryPowerChart";
import { getCurrentMIlitaryPowerData } from './Maps.helpers';


function Maps() {
  const [activeCountry, setActiveCountry] = useState([]); // getter i setter i wartoscia poczatkowa []
  const [data, setData] = useState([// getter i setter i wartoscia poczatkowa lebel i data
    {
      label: 'Potencjał militarny',
      data: []
    }
  ]);

  // funkcja sprawdzajaca czy dany kraj jest aktywny 
  const isInActiveCountry = code =>
    activeCountry.find(currentCode => currentCode === code) ? true : false;

    // funkcja nasłuchujaca zmiany kraju 
  const onChangeHandler = event => {
    const { target } = event;
    const ISO_Name = target.id.split("-")[2];

    const countryNow = event.target.checked
      ? [...activeCountry, ISO_Name]
      : activeCountry.filter(name => name !== ISO_Name);

    setActiveCountry(countryNow);

    setData([
      {
        label: countryNow.reduce((previousValue, currentValue) => previousValue === '' ? currentValue : previousValue + ' + ' + currentValue, ''),
        data: getCurrentMIlitaryPowerData(countryNow)
      }
    ])

  };
// funkcja rwenderujaca checkoxy
  const renderInputCheckbox = (country, index) => {
    const currentCountry = country["kraj"];
    const currentCountryCode = country["kod"];

    return (
      <Form.Group key={index} controlId={`coutry-checkbox-${currentCountry}`} style={{ width: '50%', maxHeight: '300px', display: 'inline-block' }}>
        <Form.Label value={currentCountryCode}>{currentCountry}</Form.Label>
        <input
          type="checkbox"
          id={`coutry-checkbox-${currentCountryCode}`}
          className={"ml-3"}
          value={currentCountryCode}
          onChange={onChangeHandler}
          checked={isInActiveCountry(currentCountryCode)}
        />
      </Form.Group>
    );
  };

  return (
    <Container fluid>
      <Row>
        <Col lg={4}>
          <fieldset>
            <legend>Lista państw:</legend>
            <Form>{COUNTRIES.map(renderInputCheckbox)}</Form>
          </fieldset>
        </Col>
        <Col lg={8}>
          <Map
            activeCountry={activeCountry}
            setActiveCountry={setActiveCountry}
            setData={setData}
          />
          <fieldset className="mt-3">
            <legend className="text-center">Parametry i wykres:</legend>
            <MIlitaryPowerChart data={data} />
          </fieldset>
        </Col>
      </Row>
    </Container>
  );
}

export default Maps;
