import React, { useState } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Tab from 'react-bootstrap/Tab'
import Tabs from 'react-bootstrap/Tabs'
import { COUNTRIES } from "data/countries";
import { labels } from "data/dataLabels";
import { BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Legend, Bar } from 'recharts'

const Comparison = () => {

    // getter i setter wartosci 
    const [firstCountry, setFirstCountry] = useState(null) //useState -> definiowanie mechanizmu Stanu dla zmiennej firstCountry z wartoscia poczatkowa null
    const [secondCountry, setSecondCountry] = useState(null)
    const [selectedTab, setSelectedTab] = useState(labels[0].key)

    // zmienna przechowujaca tablice obiektów HTML mapująca wartosci z zbioru danych COUNTRIES na elementy OPTION dla tagu SELECT  
    const createOptions = COUNTRIES.map((county, index) =>
        <option key={index} value={county['kod']}>{county['kraj']}</option>
    );

    // funkcja z parametrem atywnego typu SELECTA (first/sec) zwracjaca funkcje z parametrem eventu
    const onCountryChangeHandler = selectedCountryField => event => {
        const countryId = event.target.value; // zapisywaie countryId na podstawie wartoci elementu wybranego w SELECT
        const findCountry = countryToFind => COUNTRIES.find(country => country.kod === countryToFind); // funkcja przyjmujaca parametr kodu kraju wyszukujaca kraj w tablicy COUNTRIES o kodzie podanwgo kraju
        selectedCountryField === 'first' ? setFirstCountry(findCountry(countryId)) : setSecondCountry(findCountry(countryId)); // jesli wybrany SELECT jest 'pierwszym' ustaw znaleziony kraj w tym elemencie, jesli nie utaw go w drugim
    }

    // funkcja ustawiajaca aktywna karte
    const onTabSelectHandler = value => setSelectedTab(value);

    // funkcja zwracjaca informaje o wybranym kraju i jednostach w aktywnej kategorii
    const createCountryInfoDataLabel = countryCode => {
        const country = COUNTRIES.find(country => country.kod === countryCode);
        const label = labels.find(label => label.key === selectedTab);
        return <span>{label.name} - {country[label.key]}</span>
    }

    // funkcja sprawdzajaca czy dwa elementy SELECT sa wypełnione
    const isValueToCompare = () => secondCountry && firstCountry

    // ustawienie danych do Chartu z Labelem
    const getChart = () => {

        const value = firstCountry && secondCountry ? [
            {
                label: firstCountry.kraj,
                data: [[labels.find(label => label.key === selectedTab).name, parseInt(firstCountry[selectedTab])]]
            },
            {
                label: secondCountry.kraj,
                data: [[labels.find(label => label.key === selectedTab).name, parseInt(secondCountry[selectedTab])]]
            }
        ] : []


        const labelsArr = [value[0].label, value[1].label]
        const chartData = [{
            name: value[0].data[0][0],
            [labelsArr[0]]: value[0].data[0][1],
            [labelsArr[1]]: value[1].data[0][1]
        }]

        console.log(chartData, value)
        return (
            <BarChart width={850} height={250} data={chartData} layout="vertical" >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis hide axisLine={false} type="number" />
                <YAxis
                    yAxisId={0}
                    dataKey='name'
                    type="category"
                    axisLine={false}
                    tickLine={false}
                />
                <Tooltip />
                <Legend />
                <Bar dataKey={labelsArr[0]} fill="blue" />
                <Bar dataKey={labelsArr[1]} fill="red" />
            </BarChart>)
    }



    return (
        <Container fluid>
            <Row>
                <Col lg={12}>
                    <div>
                        <select onChange={onCountryChangeHandler('first')} name="firstCountry" id="firstCountrySelectField">
                            <option key={-1} value={''}>Wybierz kraj</option>
                            {createOptions}
                        </select>
                        <select onChange={onCountryChangeHandler('second')} name="secondCountry" id="secondCountrySelectField">
                            <option key={-1} value={''}>Wybierz kraj</option>
                            {createOptions}
                        </select>
                    </div>
                </Col>
            </Row>
            <Row>
                <Col lg={12}>
                    <Tabs defaultActiveKey={labels[0].key} onSelect={onTabSelectHandler} id="comparation-tabs">
                        {labels.map((label, index) => <Tab key={index} eventKey={label.key} title={label.name}></Tab>)}
                    </Tabs>
                </Col>
            </Row>
            {isValueToCompare() && (
                <Row>
                    <Col lg={12}>
                        <div>
                            <div>
                                <div>
                                    <p>{firstCountry.kraj}</p>
                                    {createCountryInfoDataLabel(firstCountry.kod)}
                                </div>
                                <div>
                                    <p>{secondCountry.kraj}</p>
                                    {createCountryInfoDataLabel(secondCountry.kod)}
                                </div>
                            </div>
                        </div>
                        <div
                            style={{
                                width: "80%",
                                height: "300px"
                            }}
                        >

                            {getChart()}
                        </div>
                    </Col>
                </Row>
            )}
        </Container>
    )
}

export default Comparison;