import React from "react";
import Rank from "components/Rank/Rank";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { MIlitaryPowerChart } from "components/MilitaryPowerChart/MilitaryPowerChart";
import { COUNTRIES } from "data/countries";

const RankView = () => {
    // sorotowanie krajów na podstawi potencjału globalnego malejaco 
    const sortedCountries = COUNTRIES.sort((a, b)=> parseInt(a['pbj_globalny'],10) > parseInt(b['pbj_globalny'],10) ? -1 : 1);
    // prztwarzanie danych
    const data = sortedCountries.map(country => {
        return [country.kraj, parseInt(country['pbj_globalny'],10)];
    });

    // ustawienie czartu
    const dataForChart = [
        {
            label: 'Potencjał globalny kraju',
            data
        }
    ]

    return (<Container fluid>
      <Row>
        <Col lg={4}>
            <Rank sortedCountries={sortedCountries}/>
        </Col>
        <Col lg={8}>
            <MIlitaryPowerChart data={dataForChart}/>
        </Col>
      </Row>
    </Container>)
}

export default RankView;