
import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import NotificationSystem from "react-notification-system";

import Sidebar from "components/Sidebar/Sidebar";

import { style } from "variables/Variables.jsx";

import routes from "routes.js";

import image from "assets/img/sidebar-new.jpg";

class Admin extends Component {

  constructor(props) {
    super(props);

    // ustawienie stanu poczatkowego zmiennych stanowych 
    this.state = {
      _notificationSystem: null, // notka poczatkowa nie ustawiona 
      image: image,
      color: "black",
      hasImage: true,
      fixedClasses: "dropdown show-dropdown open"
    };
  }

  // funkcja nasłuchujaca klikniecia notki 
  handleNotificationClick = position => {
    var color = Math.floor(Math.random() * 4 + 1); // ustyawiernie losowegho koloru notatki
    var level;

    // wygenerowanie typu informacvji na podstrawie wartosci koloru
    switch (color) {
      case 1:
        level = "success";
        break;
      case 2:
        level = "warning";
        break;
      case 3:
        level = "error";
        break;
      case 4:
        level = "info";
        break;
      default:
        break;
    }

    // ustawienie notatki
    this.state._notificationSystem.addNotification({
      title: <span data-notify="icon" className="pe-7s-gift" />,
      message: (
        <div>
          Welcome to <b>Light Bootstrap Dashboard</b> - a beautiful freebie for
          every web developer.
        </div>
      ),
      level: level,
      position: position,
      autoDismiss: 15
    });
  };

  // funkcja ustawijajaca przekierowania 
  getRoutes = routes => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        return (
          <Route
            path={prop.layout + prop.path}
            render={props => (
              <prop.component
                {...props}
                handleClick={this.handleNotificationClick}
              />
            )}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  getBrandText = path => {
    // iteracja przez wszystkie elementu nawigacyjne
    for (let i = 0; i < routes.length; i++) {
      if ( // sprawdzenie czy aktywna sciezka jest ta znaleziona 
        this.props.location.pathname.indexOf(
          routes[i].layout + routes[i].path
        ) !== -1
      ) {
        return routes[i].name; // zwrócenie nazwy widoku
      }
    }
    return "Brand"; // zwrćenie słowa w przypadku braku dopasowania 
  };

  // funcka uistawiajaca zdjecie w tle
  handleImageClick = image => {
    this.setState({ image: image });
  };

  // funcka uistawiajaca kolor
  handleColorClick = color => {
    this.setState({ color: color });
  };


  //funkcja ustawiajaca kontorlke czy ma byc zdjecie 
  handleHasImage = hasImage => {
    this.setState({ hasImage: hasImage });
  };

  // funkcja ustawiajaca panel na urzadzebniach mobilnyvh
  handleFixedClick = () => {
    if (this.state.fixedClasses === "dropdown") {
      this.setState({ fixedClasses: "dropdown show-dropdown open" });
    } else {
      this.setState({ fixedClasses: "dropdown" });
    }
  };

  // funmkcja odpalajaca sie na poczatku przy renderowaniu
  componentDidMount() {
    // ustawienie referencji do notatki
    this.setState({ _notificationSystem: this.refs.notificationSystem });
    var _notificationSystem = this.refs.notificationSystem;
    var color = Math.floor(Math.random() * 4 + 1);
    var level;
    switch (color) {
      case 1:
        level = "success";
        break;
      case 2:
        level = "warning";
        break;
      case 3:
        level = "error";
        break;
      case 4:
        level = "info";
        break;
      default:
        break;
    }

    _notificationSystem.addNotification({
      title: <span data-notify="icon" className="pe-7s-gift" />,
      message: (
        <div>
          Welcome to <b>Light Bootstrap Dashboard</b> - a beautiful freebie for
          every web developer.
        </div>
      ),
      level: level,
      position: "tr",
      autoDismiss: 15
    });
  }

  // funkcja odpalajaca sie za kazdym renderem komponentu (wiele razy)
  componentDidUpdate(e) {
    if ( // sprawdzenie szerokosci ekranu i historii atywnej sciezki
      window.innerWidth < 993 &&
      e.history.location.pathname !== e.location.pathname && // porownanie sciezki wczesniejszej i obecnej
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
    }
    if (e.history.action === "PUSH") { // jesli ostatnia akcja było wstaweienie nowego linku do histgorii
      document.documentElement.scrollTop = 0; // ustawienie widoku strony na sama gore
      document.scrollingElement.scrollTop = 0;
      this.refs.mainPanel.scrollTop = 0;
    }
  }

  render() {
    return (
      <div className="wrapper">
        <NotificationSystem ref="notificationSystem" style={style} />
        <Sidebar {...this.props} routes={routes} image={this.state.image}
        color={this.state.color}
        hasImage={this.state.hasImage}/>
        <div id="main-panel" className="main-panel" ref="mainPanel">
          <Switch>{this.getRoutes(routes)}</Switch>
        </div>
      </div>
    );
  }
}

export default Admin;
