import React, { memo } from "react";
import {
  ComposableMap,
  Geographies,
  Geography,
  Graticule
} from "react-simple-maps";
import { countryStateStyles } from './MapChart.styles'
import { getCurrentMIlitaryPowerData } from './../../views/maps/Maps.helpers'

const geoUrl =
  "https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json";


const MapChart = ({ setTooltipVisibility, activeCountry, setActiveCountry, setData }) => {

  // funkcja przetwartzajaca klikniecie na kraj na mapie
  const onClickHandler = event => {
    const { target } = event;
    let ISO_Name = target.id.split('-')[1]; // pobranie nazy kraju z ID
    if (ISO_Name === '') {
      ISO_Name = 'XK'
    }

    // usuwanie lub dodawanie kraju do listy aktywnych
    const currentCountry = activeCountry.find(country => country === ISO_Name) ? activeCountry.filter(name => name !== ISO_Name) : [...activeCountry, ISO_Name];

    // nowe dane do chartu 
    setData([
      {
        label: currentCountry.reduce((previousValue, currentValue) => previousValue === '' ? currentValue : previousValue + ' + ' + currentValue, ''),
        data: getCurrentMIlitaryPowerData(currentCountry)
      }
    ])

    // ustawienie aktywnego kraju
    setActiveCountry(currentCountry);

  };

  // funkcvja sprawdzajaca czyt dany kraj jest aktywny 
  const isElementActive = ISO_CURRENT_NAME => activeCountry.find(name => name === ISO_CURRENT_NAME);

  // funkcja pobierajaca kolor kraju na podstawie jego istnienia w tablicy aktywnych krajów 
  const getColor = ISO_CURRENT_NAME => {
    switch (ISO_CURRENT_NAME) {
      case 'POL':
        return countryStateStyles.pol;
      case 'NOR':
        return countryStateStyles.nor;
      case 'DEU':
        return countryStateStyles.deu;
      case '-99':
        return countryStateStyles.xk;
      case 'RUS':
        return countryStateStyles.rus;
      case 'UKR':
        return countryStateStyles.ukr;
      case 'GBR':
        return countryStateStyles.gbr;
      case 'TUR':
        return countryStateStyles.tur;
      case 'FRA':
        return countryStateStyles.fra;
      case 'ITA':
        return countryStateStyles.ita;
      case 'ESP':
        return countryStateStyles.esp;

      case 'ROU':
        return countryStateStyles.rou;
      case 'GRC':
        return countryStateStyles.grc;
      case 'BLR':
        return countryStateStyles.blr;
      case 'CHE':
        return countryStateStyles.che;
      case 'SRB':
        return countryStateStyles.srb;
      case 'FIN':
        return countryStateStyles.fin;
      case 'NLD':
        return countryStateStyles.nld;
      case 'BGR':
        return countryStateStyles.bgr;
      case 'PRT':
        return countryStateStyles.prt;
      case 'BEL':
        return countryStateStyles.bel;

      case 'HUN':
        return countryStateStyles.hun;
      case 'AUT':
        return countryStateStyles.aut;
      case 'DNK':
        return countryStateStyles.dnk;

      case 'CZE':
        return countryStateStyles.cze;
      case 'HRV':
        return countryStateStyles.hrv;
      case 'ALB':
        return countryStateStyles.alb;

      case 'SWE':
        return countryStateStyles.swe;
      case 'SVK':
        return countryStateStyles.svk;
      case 'LTU':
        return countryStateStyles.ltu;

      case 'MNE':
        return countryStateStyles.mne;
      case 'BIH':
        return countryStateStyles.bih;
      case 'LVA':
        return countryStateStyles.lva;
      case 'SVN':
        return countryStateStyles.svn;
      case 'ISL':
        return countryStateStyles.isl;
      case 'EST':
        return countryStateStyles.est;
      case 'LUX':
        return countryStateStyles.lux;
      case 'IRL':
        return countryStateStyles.irl;
      case 'LIE':
        return countryStateStyles.lie;
      case 'KAZ:':
        return countryStateStyles.kaz;
      case 'MKD':
        return countryStateStyles.mkd;
      case 'MDA':
        return countryStateStyles.mda;
      default:
        return isElementActive(ISO_CURRENT_NAME) ? countryStateStyles.active : countryStateStyles.inactive;
    }
  }

  // funkcja ustawiajaca widocznosc tabeli  
  const onWheelCaptureHandler = event => {
    const { target } = event;
    let ISO_Name = target.id.split('-')[1];
    if (ISO_Name === '') {
      ISO_Name = 'XK'
    }
    setTooltipVisibility({
      isVisible: true,
      country: ISO_Name
    });
  }

  // funcja ukrywajaca tabele z danymi kraju
  const onMouseLeaveHandler = event => {
    setTooltipVisibility({
      isVisible: false,
      country: ''
    });
  }

  return (
    <>
      <ComposableMap data-tip="" projectionConfig={{
        rotate: [-20.0, -52.0, 0],
        scale: 800
      }}>
        <Graticule stroke="#EAEAEC" />
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map(geo => (
              <Geography
                id={`map-${geo.properties.ISO_A3}`}
                key={geo.rsmKey}
                onWheelCapture={onWheelCaptureHandler}
                onMouseLeave={onMouseLeaveHandler}
                geography={geo}
                onClick={onClickHandler}
                style={getColor(geo.properties.ISO_A3)}
              />
            )
            )
          }
        </Geographies>
      </ComposableMap>
    </>
  );
};

export default memo(MapChart);
