import React, { Component } from "react";
import { NavLink } from "react-router-dom";

import logo from "assets/img/reactlogo.png";
import './Sidebar.css';

class Sidebar extends Component {
  // konstruktor ustawiajacy stan poczatkowy szerokosci okna
  constructor(props) {
    // przekazanie elementu props z komponentu nadrzednego
    super(props);
    this.state = {
      width: window.innerWidth
    };
  }
  // funkcje sprawdzajaca aktywny link 
  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }

  // funkcja ustawiajaca nowa szerokosc okna w zmiernnej stanowej komponentu
  updateDimensions() {
    this.setState({ width: window.innerWidth });
  }
  // funkcja cyklu zycia komponentu, wywołujaca sie podczas jego monntowania 
  componentDidMount() {
    this.updateDimensions(); // wywołanie ustawienia szerokosci ekranu
    window.addEventListener("resize", this.updateDimensions.bind(this)); // ustawienie nasłuchu zmiany szerokosci ekranu
  }
  render() {
    const sidebarBackground = {
      backgroundImage: "url(" + this.props.image + ")"
    };
    return (
      <div
        id="sidebar"
        className="sidebar"
        data-color={this.props.color}
        data-image={this.props.image}
      >
          {this.props.hasImage ? (
            <div className="sidebar-background" style={sidebarBackground} />
          ) : (
            null
          )}
        <div className="logo">
          <a
            href="https://www.creative-tim.com?ref=lbd-sidebar"
            className="simple-text logo-mini"
          >
            <div className="logo-img">
              <img src={logo} alt="logo_image" />
            </div>
          </a>
          <a
            href="https://www.creative-tim.com?ref=lbd-sidebar"
            className="simple-text logo-normal"
          >
            Creative Tim
          </a>
        </div>
        <div className="sidebar-wrapper">
          <ul className="nav nav-custom" >
            {this.props.routes.map((prop, key) => { // wystwietlenie elementow nawigacyjnych map, rank, compere
              if (!prop.redirect)
                return (
                  <li
                    className={
                      prop.upgrade
                        ? "active active-pro"
                        : this.activeRoute(prop.layout + prop.path) 
                    }
                    key={key}
                  >
                    <NavLink
                      to={prop.layout + prop.path} // ustawienia mioejswca nawigowania po klikanieciu elementu, łączenie nazwy modułu i nazwy sciezki reprezentujacej komponent
                      className="nav-link"
                      activeClassName="active"
                    >
                      <i className={prop.icon} />
                      <p>{prop.name}</p>
                    </NavLink>
                  </li>
                );
              return null;
            })}
          </ul>
        </div>
      </div>
    );
  }
}

export default Sidebar;
