import React from "react";
import {BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Legend, Bar}  from 'recharts'

export const MIlitaryPowerChart = ({ data }) => { // destruklturyzajca props i wyciagniecie klucza data

  if(data){
    const {label, data: dataValue} = data[0];
    const chartData = [Object.assign({}, ...dataValue.map(stat => ({name: label, [stat[0]]: stat[1] })))]

    return (
      <div
        style={{
          width: "80%",
          height: "300px",
          marginTop: "90px"
        }}
      >
        <BarChart width={850} height={250} data={chartData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          {dataValue.map(stat =>  <Bar dataKey={stat[0]} fill="green" /> )}
        </BarChart>
      </div>
    );
  } else {
    return (<p>Brak danych do wyświetlenia</p>)
  }

};
