import React, { useState } from "react";
import ReactTooltip from "react-tooltip";
import { Table } from "react-bootstrap";

import MapChart from "./../MapChart/MapChart";
import { COUNTRIES } from "data/countries";
import { getCurrentMIlitaryPowerData } from "./../../views/maps/Maps.helpers";
import "./Maps.css";
import MapLegend from "components/MapLegend/MapLegend";

export const Map = props => {

  // inicjowanie zmiennej contentVisibilioty i jej settera 
  const [contentVisibility, setContentVisibility] = useState({
    isVisible: false, // wartosc poczatkowa 
    country: "" // wartosc poczatkowa 
  });

  // funkcja reagujaca na klikniecie flagi
  const onFlagClickHandler = event => {
    const { target } = event; // wyciagniecie elementu klikniertego z event
    const { activeCountry, setData, setActiveCountry } = props;  // wyciagniecie kluczy z props
    const ISO_Name = target.id.split("-")[1]; // wyciagniecie nazwy z id konkretnego elementu 
    // znajdowanie obecnego kraju na podstawie nazwy ISO z konkretnego elementu 
    const currentCountry = activeCountry.find(country => country === ISO_Name)
      ? activeCountry.filter(name => name !== ISO_Name) // jesli znaleziono kraj to usun ten kraj z listy 
      : [...activeCountry, ISO_Name]; // jesli nie to dodaj kraj do listy

    // sette zmiennej data
    setData([ // tablica obiektów z kluczami data i label
      {
        // redukowanie tablicy krajów poprzez budowanie z nich cioagu znakowego typu 'nazwa kraju - nazwa kraju .....'
        label: currentCountry.reduce( 
          (previousValue, currentValue) =>
            previousValue === ""
              ? currentValue
              : previousValue + " + " + currentValue,
          ""
        ),
        // ustawiania danych do wyswiettlenia w charcie
        data: getCurrentMIlitaryPowerData(currentCountry)
      }
    ]);
    // ustawienie aktynwego kraju
    setActiveCountry(currentCountry);
  };

  // funkcja gfenerujaca flagi kanady i Usa
  const renderFlagOFMissingCountries = () => (
    <div className="missing-countries-coutainer">
      <img
        id="flag-CDN"
        onClick={onFlagClickHandler}
        onWheelCapture={onWheelCaptureHandler}
        onMouseLeave={onMouseLeaveHandler}
        src="data:image/jpeg;base64,"
        alt="Canada"
      />
      <img
        onClick={onFlagClickHandler}
        onWheelCapture={onWheelCaptureHandler}
        onMouseLeave={onMouseLeaveHandler}
        id="flag-USA"
        src="https://www.iconexperience.com/_img/v_collection_png/256x256/shadow/flag_usa.png"
        alt="USA"
      />
    </div>
  );

  // funkcja ustawiajaca dane do tabeli o informacji o kraju
  const onWheelCaptureHandler = event => {
    const { target } = event;
    const ISO_Name = target.id.split("-")[1];

    // ustawienie widocznosci tabeli
    setContentVisibility({
      isVisible: true,
      country: ISO_Name
    });
  };

  // funklcja chowajaca tabele 
  const onMouseLeaveHandler = event => {
    setContentVisibility({
      isVisible: false,
      country: ""
    });
  };

  // funkcja renderujaca tabele 
  const renderTableComponent = (country) => {
    const currentCountry = COUNTRIES.find(
      countryObject => countryObject["kod"] === country
    );

    if (!currentCountry) {
      return <></>;
    }

    // tabela danych 
    return (
      <Table bordered className="mt-3" variant="dark">
        <tbody>
          <tr>
            <th>Nazwa kraju</th>
            <td>{currentCountry["kraj"]}</td>
          </tr>
          <tr>
            <th>Potencjał globalny</th>
            <td>{currentCountry["pbj_globalny"]}</td>
          </tr>
          <tr>
            <th>Zołnierze</th>
            <td>{currentCountry["zolnierze"]}</td>
          </tr>
          <tr>
            <th>Zołnierze rezerwy</th>
            <td>{currentCountry["zolnierze_rezerwy"]}</td>
          </tr>
          <tr>
            <th>Czolgi</th>
            <td>{currentCountry["czolgi"]}</td>
          </tr>
          <tr>
            <th>Bojowe wozy opancerzone</th>
            <td>{currentCountry["bwo"]}</td>
          </tr>
          <tr>
            <th>Artyleria</th>
            <td>{currentCountry["artyleria"]}</td>
          </tr>
          <tr>
            <th>Samoloty</th>
            <td>{currentCountry["samoloty"]}</td>
          </tr>
          <tr>
            <th>Smigłowce</th>
            <td>{currentCountry["smiglowce"]}</td>
          </tr>
          <tr>
            <th>Okrety</th>
            <td>{currentCountry["okrety"]}</td>
          </tr>
        </tbody>
      </Table>
    );
  };
  return (
    <div>
      {renderFlagOFMissingCountries()}
      <MapLegend/> 
      <MapChart
        setData={props.setData}
        activeCountry={props.activeCountry}
        setActiveCountry={props.setActiveCountry}
        setTooltipVisibility={setContentVisibility}
      />
      <ReactTooltip place="right">
        {contentVisibility.isVisible && renderTableComponent(contentVisibility.country)}
      </ReactTooltip>
    </div>
  );
};
